import React from "react";
import {
  GoogleMap,
  LoadScript,
  Marker,
  InfoWindow,
} from "@react-google-maps/api";
import { LocationMarkerIcon } from "@heroicons/react/solid";

const MapContainer = (props) => {
  const [selected, setSelected] = React.useState(null);

  const mapStyles = {
    height: "100%",
    width: "100%",
  };

  const defaultCenter = {
    lat: 42.16887,
    lng: -88.07164,
  };

  const onSelect = (item) => {
    // alert("aldskfjladksjfl");
    setSelected(item);
  };

  return (
    <LoadScript googleMapsApiKey="AIzaSyD3S0yeE5zI5gaA-IaC2HE8ZEd9wdiyvSs">
      <GoogleMap mapContainerStyle={mapStyles} zoom={17} center={defaultCenter}>
        {props.locations.map((item) => {
          return (
            <Marker
              key={item.name}
              position={{
                lat: item.lat,
                lng: item.lng,
              }}
              onClick={() => {
                onSelect(item);
              }}
            />
          );
        })}
        {selected != null ? (
          <InfoWindow
            position={{ lat: selected.lat, lng: selected.lng }}
            clickable={true}
            onCloseClick={() => {
              setSelected(null);
            }}
          >
            <p>{selected.name}</p>
          </InfoWindow>
        ) : (
          <div />
        )}
      </GoogleMap>
    </LoadScript>
  );
};

const locations = [
  {
    name: "Deer Park, IL",
    address: "21020 N Rand Road, Deer Park, IL 60047",
    lat: 42.16884,
    lng: -88.07164,
    comingSoon: false,
  },
  {
    name: "Glenview, IL",
    address: "954 Harlem Ave. Glenview, IL 60025",
    lat: 42.07196,
    lng: -87.80638,
    comingSoon: true,
  },
];

export function Location(props) {
  return (
    <div className="w-screen h-screen max-h-96 my-10 bg-green-300 relative">
      <div className=" md:max-w-lg md:absolute md:m-10 md:mt-20 max-h-56 md:max-h-80 p-2 md:p-5 md:rounded-lg bg-white border-t-2 md:border-2 md:z-10 overflow-y-auto border-taLightRed">
        <h1 className="text-2xl font-bold">LOCATIONS</h1>
        <ul className="divide-y divide-gray-200">
          {locations.map((aLocation) => (
            <li key={aLocation.name} className="">
              <button className="flex justify-between items-center rounded-md hover:bg-gray-200 transition duration-500 border-none m-2 p-2 w-full ">
                <div className="ml-3">
                  <div className="flex items-center">
                    <p className="text-xl font-medium text-gray-900 text-left">
                      {aLocation.name}
                    </p>
                    {aLocation.comingSoon ? (
                      <h1 className="text-xl px-1 ml-6 font-bold text-taLightRed border-2 border-taRed rounded-md">
                        COMING SOON!!!
                      </h1>
                    ) : (
                      <div />
                    )}
                  </div>

                  <p className="text-sm text-gray-500 text-left">
                    {aLocation.address}
                  </p>
                </div>
                <LocationMarkerIcon className="h-6 w-6 text-taBlue mx-2" />
              </button>
            </li>
          ))}
        </ul>
      </div>
      <div className="h-full w-full bg-blue-700 absolute max-h-96 z-0">
        <MapContainer className="h-full" locations={locations} />
      </div>
    </div>
  );
}
